/* eslint-disable react/jsx-no-target-blank */
import { makeStyles } from "@material-ui/core/styles";
import React from "react";

const useStyles = makeStyles(() => ({
  para: {
    fontSize: "15px",
    lineHeight: "30px"
  }
}));

export default function Contact(): JSX.Element {
  const classes = useStyles();

  return (
    <div>
      <ul className={classes.para}>
        <li>客户服务群：110274790（加群请说明校区、专业、年级）</li>
        <li>
          新浪微博：
          <a
            href="http://weibo.com/cqulanunion"
            target="_blank"
            className="url"
          >
            http://weibo.com/cqulanunion
          </a>
        </li>
        <li>
          腾讯微博：
          <a href="http://t.qq.com/cqulanunion" target="_blank" className="url">
            http://t.qq.com/cqulanunion
          </a>
        </li>
        <li>
          电子邮箱：
          <a href="mailto:lanunion@cqu.edu.cn" target="_blank" className="url">
            lanunion@cqu.edu.cn
          </a>
        </li>
        <li>办公室地址：重庆大学B区材料楼二楼最右</li>
      </ul>
    </div>
  );
}
