import { makeStyles } from "@material-ui/core/styles";
import React from "react";

const useStyles = makeStyles(() => ({
  title: {
    fontSize: "23px",
    color: "#0772A1"
  },
  para: {
    textIndent: "2em",
    fontSize: "15px",
    lineHeight: "30px"
  }
}));

export default function About(): JSX.Element {
  const classes = useStyles();

  return (
    <>
      <h2 className={classes.title}>关于蓝盟</h2>
      <div>
        <p className={classes.para}>
          重庆大学蓝盟（英文名lanunion）是在重庆大学信息化办公室指导下成立的校级学生社团，是一个为重庆大学A、B、C区师生免费提供计算机系统、软件与网络维护服务的志愿者组织。“蓝客”即蓝盟的正式成员，他们利用自己的电脑知识无偿为大家进行计算机系统、软件与网络维护。
        </p>
      </div>
    </>
  );
}
