import { makeStyles } from "@material-ui/core/styles";
import React from "react";

const useStyles = makeStyles(() => ({
  homeMud: {
    float: "left",
    fontFamily: "SimHei",
    fontSize: "200px",
    textAlign: "center",
    height: "130px"
  },
  homeWordsMud: {
    float: "left",
    fontSize: "40px",
    paddingTop: "40px"
  },
  span: {
    color: "#EE3B80"
  },
  lanMen: {
    float: "right"
  },
  clear: {
    clear: "both"
  }
}));

export default function Title(): JSX.Element {
  const classes = useStyles();

  return (
    <>
      <div className={classes.homeMud}>“</div>
      <div className={classes.homeWordsMud}>
        我们是为重庆大学在校师生
        <br />
        <span className={classes.span}>免费修电脑</span>的校级学生社团
      </div>
      <div className={classes.homeMud}>”</div>
      <div className={classes.lanMen}>——重庆大学蓝盟</div>
      <div className={classes.clear}></div>
    </>
  );
}
